import { useEffect, useContext } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { Formik } from "formik";
import * as Yup from "yup";
import { InputBox, PasswordBox, ButtonBox, CheckBox, Buttons, SelectBox, TextareaBox } from "../../../components/RenderFroms";
import { BiSolidUser, BiSolidLockAlt, BiRightArrowAlt } from "react-icons/bi";
import Breadcrumbs from "../../../components/Breadcrumbs";
import ListTitle from "../../../components/ListTitle";
import { useFetch } from "../../../contexts/useFetch";
import { useFetchSingle } from "../../../contexts/useFetchSingle";
import { usePost } from "../../../contexts/usePost";
import { usePatch } from "../../../contexts/usePatch";
const resource = "suppliers";

const defaultValue = {
	supplierType: "",
	supplierName: "",
	supplierAddress: "",
	supplierSuburb: "",
	supplierState: "",
	supplierCountry: "",
	supplierPostalCode: "",
	supplierEmailAddress: "",
	supplierPhoneNumber: "",
	supplierMobileNumber: "",

}

export default function AddSupplier() {



	const navigate = useNavigate();
	const { id } = useParams();

	const validationSchemaInfo = Yup.object().shape({
		supplierType: Yup.string().required("User namr is required"),
		supplierName: Yup.string().required("Name is required"),
		 


	});

	const { fetchSingle, data: suppliers, loading: loadingData } = useFetchSingle();
	useEffect(() => {
		fetchSingle(resource, id)
	}, [id])

	const { create, data: respond, loading } = usePost();
	const { edit, data: respond1, loading: loading1 } = usePatch();
	const handleUpdate = (values: any) => {
		 
		if (id) {
		 
			edit(resource, values)
		} else {
			 
			create(resource, values)
		}
	}

	useEffect(() => {
		if ((respond as any)?.status == 201) {
			navigate(-1)
		}
		if ((respond1 as any)?.status == 200) {
			navigate(-1)
		}
	}, [respond, respond1])

	if (loadingData) {
		return <>loading</>
	}

	const statusOptions = [{ label: 'Local', value: 'local' }, { label: 'Overseas', value: 'overseas' }]

	return (
		<>

			<Breadcrumbs root="suppliers" pageName={`${id ? 'Edit' : 'Add'} Suppliers`} />

			<div className="main-content-body">
				<div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
					<div className="row">
						<div className="card">

							<ListTitle ListName={`${id ? 'Edit' : 'Add New'} Supplier (* indicates mandatory field(s)).`} />


							<div className="form-horizontal has-validation-callback">
								<Formik
									initialValues={suppliers ? suppliers : defaultValue}
									validationSchema={validationSchemaInfo}
									onSubmit={(values: any) => handleUpdate(values)}
								>
									{({ handleChange, handleBlur, handleSubmit, values, errors }) => (

										<>
											<div className="card-body">

												<SelectBox
													required={true}
													options={statusOptions}
													name="supplierType"
													lable="Supplier Type"
													placeholder="Select One"
												/>

												<InputBox
													required={true}
													name="supplierName"
													lable="Supplier Name"
													placeholder=""
												/>

												<TextareaBox

													name="supplierAddress"
													lable="Supplier Address"
													placeholder=""
												/>

												<InputBox

													name="supplierSuburb"
													lable="Supplier Suburb"
													placeholder=""
												/>

												<InputBox

													name="supplierState"
													lable="Supplier State"
													placeholder=""
												/>

												<InputBox

													name="supplierCountry"
													lable="Supplier Country"
													placeholder=""
												/>

												<InputBox

													name="supplierPostalCode"
													lable="Supplier Postal Code"
													placeholder=""
												/>

												<InputBox

													name="supplierEmailAddress"
													lable="Supplier Email ID"
													placeholder=""
												/>

												<InputBox

													name="supplierPhoneNumber"
													lable="Supplier Phone No."
													placeholder=""
												/>

												<InputBox

													name="supplierMobileNumber"
													lable="Supplier Mobile No."
													placeholder=""
												/>
												<div className="clear"></div>
											</div>


											<div className="card-action">
												<label htmlFor="AddGroup_Submit" className="col-lg-4 col-md-4 col-sm-12 col-xs-12 control-label">&nbsp;</label>
												<div className="col-lg-8 col-md-8 col-sm-12 col-xs-12 no-padding mt-3 mb-3">
													<Buttons loading={loading || loading1} className="btn-success" value="Save Supplier" onClick={() => handleSubmit()} />
													<Buttons className="btn-grey" value="Go Back to List" onClick={() => navigate(-1)} />



												</div>
											</div>
											<div className="clear"></div>
										</>)}
								</Formik>
							</div>


						</div>
					</div>
				</div>
			</div>


		</>
	);
}


